import { AuthGuard, PermissionGuard } from '@abp/ng.core';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RecurringNotificationInfosComponent } from './components/recurring-notification-infos.component';

const routes: Routes = [
  {
    path: '',
    component: RecurringNotificationInfosComponent,
    canActivate: [AuthGuard, PermissionGuard],
  },
  {
    path: 'create-notification-info',
    loadChildren: () => import('../create-notification-info/create-notification-info.module').then(m => m.CreateNotificationInfoModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class RecurringNotificationInfosRoutingModule { }
