import { ValidationCallbackData } from 'devextreme/common';
import { Column } from 'devextreme/ui/data_grid';
import { filterOperationTypes } from 'src/app/shared/grid/filterOperations';
import { requiredValidationRule } from 'src/app/shared/validationRules/required';

export const columns: Column[] = [
  {
    type: 'buttons',
    fixed: true,
    buttons: [
      {
        name: 'schedule-plans',
        template: 'gridSchedulePlanDetailsButton'
      },
      {
        name: 'edit',
        template: 'grid-edit-button',
        disabled(options) {
          return !options.row?.data?.isEditable;
        },
      },
      {
        name: 'delete',
        template: 'grid-delete-button',
        disabled(options) {
          return !options.row?.data?.isDeletable;
        },
      }
    ],
  },
  {
    dataField: 'id',
    visible: false,
  },
  {
    dataField: 'concurrencyStamp',
    visible: false,
  },

  // grid item
  {
    dataField: 'SpecifiedTenantId',
    caption: '指定租戶',
    dataType: 'string',
    visible: false
  },
  {
    name: 'tenantName',
    caption: '租戶名稱',
    dataType: 'string',
    filterOperations: filterOperationTypes.text
  },
  {
    name: 'notificationCategoryName',
    caption: '分類名稱',
    dataType: 'string',
    filterOperations: filterOperationTypes.text
  },
  {
    dataField: 'name',
    caption: '通知名稱',
    dataType: 'string',
    filterOperations: filterOperationTypes.text,
    validationRules: [requiredValidationRule]
  },
  {
    name: 'notificationProviders',
    caption: '通知提供者',
    dataType: 'string',
    filterOperations: filterOperationTypes.text,
    calculateDisplayValue(rowData) {
      return rowData.notificationProviders?.map(provider => provider.displayName).join('、')
    },
    width: 160,
    allowFiltering: false,
    allowSorting: false
  },
  {
    name: 'receiverDescription',
    caption: '接收者描述',
    dataType: 'string',
    filterOperations: filterOperationTypes.text,
    allowFiltering: false,
    allowSorting: false
  },

  // master detail
  {
    type: 'detailExpand',
    fixed: true,
    fixedPosition: 'right'
  },
  {
    dataField: 'notificationDataFetchEndpoint',
    caption: '取得通知資料 Endpoint',
    dataType: 'string',
    filterOperations: filterOperationTypes.text,
    visible: false
  },
  {
    dataField: 'hookEndpoint',
    caption: 'Hook',
    dataType: 'string',
    filterOperations: filterOperationTypes.text,
    visible: false
  },
  {
    name: 'isFullyFinished',
    caption: '是否已完成',
    dataType: 'boolean',
    visible: false
  },
  {
    name: 'isDeletable',
    caption: '是否可刪除',
    dataType: 'boolean',
    visible: false
  },
  {
    name: 'isEditable',
    caption: '是否可編輯',
    dataType: 'boolean',
    visible: false
  },

  // edit form
  {
    dataField: 'title',
    caption: '標題',
    dataType: 'string',
    visible: false,
    validationRules: [requiredValidationRule]
  },
  {
    dataField: 'notificationProviderIds',
    caption: '通知提供者id (可複選)',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'notificationProviderIds',
    validationRules: [requiredValidationRule]
  },
  {
    dataField: 'isSendToAllReceivers',
    caption: '接收者',
    dataType: 'boolean',
    visible: false,
    editCellTemplate: 'isSendToAllReceivers'
  },
  {
    dataField: 'receiverIds',
    caption: '接收者 (可複選)',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'receiverIds'
  },
  {
    dataField: 'receiverGroupIds',
    caption: '接收群組 (可複選)',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'receiverGroupIds'
  },
  {
    dataField: 'topicIds',
    caption: '主題 (可複選)',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'topicIds'
  },
  {
    dataField: 'isIndependentNotificationSettingUsing',
    caption: '通知設定',
    dataType: 'boolean',
    visible: false,
    editCellTemplate: 'isIndependentNotificationSettingUsing'
  },
  {
    dataField: 'notificationSettings',
    caption: '通知設定項目',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'notificationSettings'
  },
  {
    dataField: 'notificationSchedulePlans',
    caption: '重複時間計畫',
    dataType: 'object',
    visible: false,
    editCellTemplate: 'recurringNotificationSchedulePlans'
  },
  {
    dataField: 'notificationCategoryId',
    caption: '通知分類 Id',
    dataType: 'string',
    visible: false,
    validationRules: [requiredValidationRule]
  },
  {
    dataField: 'subtitle',
    caption: '子標題',
    dataType: 'string',
    visible: false,
    validationRules: [requiredValidationRule]
  },

  // notification content
  {
    dataField: 'textContent',
    caption: '文字內容',
    dataType: 'string',
    visible: false,
    // validationRules: [requiredValidationRule]
  },
  {
    dataField: 'htmlContent',
    caption: 'Html 內容',
    dataType: 'string',
    visible: false,
    // validationRules: [requiredValidationRule]
  },
  {
    dataField: 'isFetchNotificationDataByEachReceiver',
    caption: '是否依各通知接收者取得資料',
    dataType: 'string',
    visible: false
  },
  {
    dataField: 'filePaths',
    dataType: 'string',
    visible: false
  },
  {
    dataField: 'hyperlink',
    dataType: 'string',
    visible: false
  },

  // notification template
  {
    dataField: 'isUsingTemplate',
    caption: '模板',
    dataType: 'boolean',
    visible: false,
    editCellTemplate: 'isUsingTemplate'
  },
  {
    dataField: 'textContentNotificationTemplateId',
    caption: '通知模板Id',
    dataType: 'string',
    visible: false,
    // validationRules: [requiredValidationRule]
  },
  {
    name: 'textContentNotificationTemplateName',
    caption: '通知模板名稱',
    dataType: 'string',
    visible: false,
    // validationRules: [requiredValidationRule]
  },
  {
    dataField: 'textContentNotificationTemplateArgs',
    caption: 'text content 通知模板參數',
    dataType: 'string',
    visible: false,
    // validationRules: [requiredValidationRule],
    editCellTemplate: 'textContentNotificationTemplateArgs'
  }
];