import { Component, OnInit, ViewChild } from '@angular/core';
import type { Editing, OptionChangedEvent, Properties, RowExpandedEvent, SavingEvent } from 'devextreme/ui/data_grid';
import { DxDataGridComponent } from 'devextreme-angular';
import { Column } from 'devextreme/ui/data_grid';
import { columns } from '../config/recurring-notification-infos/columns';
import { cRUDEndpoints } from '../config/recurring-notification-infos/crud-endpoints';
import { dataFieldLookupSourceMapping } from '../config/recurring-notification-infos/datafield-lookup-source-mapping';
import { relatedDataValueAndQueryStringKeyMapping } from '../config/recurring-notification-infos/datafield-value-query-string-key-mapping';
import {
    editingPropertiesForCreating,
    editingPropertiesForUpdating,
} from '../config/recurring-notification-infos/form-properties';
import DataSource from 'devextreme/data/data_source';
import { CRUDEndpoints, DataFieldLookUpSourceMapping, RelatedDataValueAndQueryStringKeyMapping } from 'src/app/shared/typing';
import { GridDataSourceService } from 'src/app/shared/service/grid-data-source.service/grid-data-source.service';
import { TemplateStoreService } from 'src/app/shared/service/template-store.service';
import { NotificationTemplateStoreService } from 'src/app/shared/service/notification-template-store/notification-template-store.service';
import { Router } from '@angular/router';
import { NotificationSchedulePlan } from '../config/notification-schedule-plan-details/typing';
import { AppRestService } from 'src/app/shared/service/rest.service';
import { TenantChooserService } from 'src/app/shared/modules/tenant-chooser/tenant-chooser.service';
import { CustomRule, ValidationCallbackData } from 'devextreme/common';

@Component({
    selector: 'app-recurring-notification-infos',
    templateUrl: 'recurring-notification-infos.component.html',
    styleUrls: ['recurring-notification-infos.component.scss']
})
/**
 * 
 */
export class RecurringNotificationInfosComponent implements OnInit {
    /**
     * 取得datagrid元件，將之存於變數grid。
     */
    @ViewChild('grid', { static: false }) grid: DxDataGridComponent;

    /**
     * devextreme data grid columns
     * 
     * 用來設定grid具有那些欄位。
     * 
     * DevExtreme官方文件: [Column]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Columns/Overview/}
     */
    columns: Column[] = columns;

    /**
     * devextreme grid datasource
     * 
     * grid dataSource, 用來連接grid與後端data的新增、讀取、修改、刪除。
     * 
     * 透過[getDataSource]{@link GridDataSourceService#getDataSource}使用[cRUDEndpoints]{@link RecurringNotificationInfosComponent#cRUDEndpoints}與[relatedDataValueAndQueryStringKeyMapping]{@link RecurringNotificationInfosComponent#relatedDataValueAndQueryStringKeyMapping}產生。
     * 
     * DevExtreme官方文件: [DataSource]{@link https://js.devexpress.com/Angular/Documentation/23_2/ApiReference/Data_Layer/DataSource/}
     */
    gridDataSource: DataSource;

    /**
     * 設定增、刪、查、改的路徑
     * 
     * 用於[getDataSource]{@link GridDataSourceService#getDataSource}接收然後利用產生[gridDataSource]{@link RecurringNotificationInfosComponent#gridDataSource}。
     */
    cRUDEndpoints: CRUDEndpoints = cRUDEndpoints;

    /**
     * 選單與資料路徑對應設定檔案
     * 
     * {@link GridFeatureDirective}接收後用來設定[DataGrid]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Getting_Started_with_DataGrid/}的新增、編輯表單裡的選單。
     */
    dataFieldLookupSourceMapping: DataFieldLookUpSourceMapping = dataFieldLookupSourceMapping;

    /**
     * 欄位、值、與回應資料結構的對應設定檔案
     * 
     * 用於[getDataSource]{@link GridDataSourceService#getDataSource}接收然後利用產生[gridDataSource]{@link RecurringNotificationInfosComponent#gridDataSource}。
    */
    relatedDataValueAndQueryStringKeyMapping: RelatedDataValueAndQueryStringKeyMapping = relatedDataValueAndQueryStringKeyMapping;

    /**
     * 新增的表單設定
     * 
     * DevExtreme官方文件: [Editing]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Editing/}
     */
    editingPropertiesForCreating: Editing = editingPropertiesForCreating;

    /**
     * 編輯的表單設定
     * 
     * DevExtreme官方文件: [Editing]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Editing/}
     */
    editingPropertiesForUpdating: Editing = editingPropertiesForUpdating;

    /**
     * 通知模板ID
     * 
     * 選擇通知模板後，藉由ID取得該模板的內容與參數
     */
    notificationTemplateId: string

    /**
     * 時間計畫細節的popup是否可見
     */
    isDetailsVisible: boolean = false

    /**
     * 存取用於時間計畫細節的資料
     */
    notificationSchedulePlans: NotificationSchedulePlan[]

    /**
     * 使用master detail功能時，記錄目前展開的Row，以在展開新的Row時，可以關閉上一個開啟的Row，確保只會有一個Row是展開的
     */
    masterDetailRowCollapseKey: string

    errorMessage = ""

    /**
     * 啟用master detail功能，並給予[template]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/Common/Object_Structures/template/}
     * 
     * DevExtreme官方文件: [Properties]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/Configuration/masterDetail/}
     */
    gridProperties: Properties = {
        masterDetail: {
            enabled: true,
            template: 'basic-master-detail-row-template'
        },
        height: '85%'
    }

    /**
     * 參考{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Editing|Editing},
     * [editCellTemplate]{@link https://js.devexpress.com/Angular/Documentation/23_2/ApiReference/UI_Components/dxDataGrid/Configuration/columns/\#editCellTemplate/},
     * [template]{@link js.devexpress.com/Angular/Documentation/23_2/ApiReference/UI_Components/dxForm/Item_Types/SimpleItem/#template/}
     * 
     * 使用[tenantChooser]{@link TenantChooserService}，當HOST指定租戶時，改變Grid中的對應的資料
     * 
     * 配置receiverIds、receiverGroupIds、topicIds的驗證規則
     * 
     * @param gridDataSourceService dxdatagrid所使用的產生datasource相關功能
     * @param templateStore 共用的dx template儲存service，用於dx-data-grid、dx-form的custom template, 參考editCellTemplate, template
     * @param notificationTemplateStore 共用的dx template儲存service，用於dx-data-grid、dx-form的custom template, 參考editCellTemplate, template,
     * 但只用於新增通知項目時使用
     * @param router 用於新增時，將頁面導向共用的新增頁面
     */
    constructor(
        private appRestService: AppRestService,
        private gridDataSourceService: GridDataSourceService,
        protected templateStore: TemplateStoreService,
        protected notificationTemplateStore: NotificationTemplateStoreService,
        protected router: Router,
        private tenantChooser: TenantChooserService
    ) {
        this.tenantChooser.tenantId$.subscribe(tenandId => {
            this.grid?.instance?.refresh()
        })

        const columnReceiverGroupIds = columns.find(c => c.dataField === 'receiverGroupIds');
        const columnTopicIds = columns.find(c => c.dataField === 'topicIds');
        const receiverIds = columns.find(c => c.dataField === 'receiverIds');
        columnReceiverGroupIds.validationRules = [this.setCustomValidationRule('receiverGroupIds')]
        columnTopicIds.validationRules = [this.setCustomValidationRule('topicIds')]
        receiverIds.validationRules = [this.setCustomValidationRule('receiverIds')]
    }

    /**
     * receiverIds、receiverGroupIds、topicIds驗證規則，當isSendToAllReceivers === true、
     * 該欄位的陣列長度至少為1、其他兩項欄位的陣列長度至少為1，其中一項符合時，驗證通過
     * 
     * 使用[reevaluate]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxValidator/Validation_Rules/CustomRule/#reevaluate}，
     * 
     * @param name 需要設置驗證規則的欄位名稱
     */
    setCustomValidationRule(name: string) {
        const optionDataTargets = ['receiverIds', 'receiverGroupIds', 'topicIds'].filter(target => target !== name)

        const customValidationRule: CustomRule = {
            type: 'custom',
            message: '接收者、接收者群組、主題必須選擇一項',
            reevaluate: true,
            validationCallback: (option: ValidationCallbackData) => {
                if (
                    option.data.isSendToAllReceivers ||
                    option.value.length ||
                    option.data[optionDataTargets[0]].length ||
                    option.data[optionDataTargets[1]].length
                ) {
                    // grid的option方法會導致重繪，暫用此法。
                    // 如果不使用json控制form格式，而是寫在html的template也許有機會可以做動態控制。
                    document.querySelector('.recurring-notification-custom-validation-group').classList.remove('error')
                    this.errorMessage = ''
                    return true
                }
                // grid的option方法會導致重繪，暫用此法。
                // 如果不使用json控制form格式，而是寫在html的template也許有機會可以做動態控制。
                document.querySelector('.recurring-notification-custom-validation-group').classList.add('error')
                this.errorMessage = '接收者、接收者群組、主題必須選擇一項'
                return false
            }
        }

        return customValidationRule
    }

    /**
     * 元件啟動後，透過[getDataSource]{@link GridDataSourceService#getDataSource}使用設定檔設定grid的dataSource - [gridDataSource]{@link RecurringNotificationInfosComponent#gridDataSource}。
     */
    ngOnInit(): void {
        this.gridDataSource = this.gridDataSourceService.getDataSource(
            cRUDEndpoints,
            relatedDataValueAndQueryStringKeyMapping
        );
    }

    /**
     * [rowExpanded]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/Events/#rowExpanded}事件觸發時，呼叫[applyyNotificationMasterDetailRowCollapse]{@link InstantNotificationInfosComponent#applyNotificationMasterDetailRowCollapse}
     * 
     * @param e 參考RowExpandedEvent
     */
    onRowExpanded(e: RowExpandedEvent) {
        this.applyNotificationMasterDetailRowCollapse(e)
    }

    /**
     * 展開master detail時，添加畫面項目需要的CSS
     * 
     * 如果[masterDetailRowCollapseKey]{@link RecurringNotificationInfosComponent#masterDetailRowCollapseKey}與按下展開按鈕的Row是一樣的時，則[masterDetailRowCollapseKey]{@link RecurringNotificationInfosComponent#masterDetailRowCollapseKey}不變
     * 
     * 記錄展開當下的[masterDetailRowCollapseKey]{@link RecurringNotificationInfosComponent#masterDetailRowCollapseKey}，並確保每次只會有一個Row被展開
     * 
     * 使用setTimeout避免NG0100{@link https://angular.dev/errors/NG0100}Error
     */
    applyNotificationMasterDetailRowCollapse(e: RowExpandedEvent) {
        setTimeout(() => {
            const tdElementCollapse = e.element.querySelector('[aria-label="Collapse"]')
            const trElementRowIndex = tdElementCollapse.parentElement.ariaRowIndex
            const trElementRow = e.element.querySelector(`tr.dx-row.dx-data-row.dx-row-lines[aria-rowindex="${trElementRowIndex}"]`)
            trElementRow.classList.add('master-detail-row-collapse')
        })

        if (e.key === this.masterDetailRowCollapseKey) {
            return
        }

        e.component.collapseRow(this.masterDetailRowCollapseKey);
        this.masterDetailRowCollapseKey = e.key
    }

    /**
     * 將該筆資料的時間計畫細節popup開啟，以及取得時間計畫細節的資料，最後再將該資料傳入時間計畫細節的data grid中
     * 
     * 打開popup時，需要使用API取得最新的時間計畫細節，由於重複通知可能每分鐘，時間計畫細節就會不一樣
     * 
     * @param notificationSchedulePlans 利用[cellInfo]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/Configuration/columns/#cellTemplate}取得欄位資料
     */
    openDetailsTable(id: string) {
        this.appRestService.request<void, { [key: string]: any }>({
            method: 'GET',
            url: `/api/app/recurring-notifications/${id}`
        }).subscribe(res => {
            this.notificationSchedulePlans = res.notificationSchedulePlans
            this.isDetailsVisible = true
        })
    }

    /**
     * 當切換 "所有人(isSendToAllReceivers === true)" ，必須在資料送出前，將receiverIds、receiverGroupIds、topicIds設為空陣列
     * 
     * 新增或編輯時，將htmlContentNotificationTemplateArgs與htmlContentNotificationTemplateId的值設為與textContentNotificationTemplateArgs、textContentNotificationTemplateId一樣
     * 
     * 參考[SavingEvent]{@link  https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/Configuration/#onSaving}
     * 
     * @param e SavingEvent
     */
    onSaving(e: SavingEvent) {
        if (e.changes[0]?.type === 'update' && e.changes[0].data.isSendToAllReceivers) {
            e.changes[0].data.receiverIds = []
            e.changes[0].data.receiverGroupIds = []
            e.changes[0].data.topicIds = []
        }

        if (e.changes[0]?.type === 'update' && e.changes[0].data?.textContentNotificationTemplateId) {
            e.changes[0].data.htmlContentNotificationTemplateId = e.changes[0].data?.textContentNotificationTemplateId
            e.changes[0].data.htmlContentNotificationTemplateArgs = e.changes[0].data?.textContentNotificationTemplateArgs || ''
        }
    }

    /**
     * 取得[notificationTemplateId]{@link RecurringNotificationInfosComponent#notificationTemplateId}
     * 
     * @param e OptionChangedEvent
     */
    onOptionChanged(e: OptionChangedEvent) {
        if (
            e.fullName !== 'editing.changes' ||
            !e.value ||
            !e.value[0]?.data ||
            !('textContentNotificationTemplateId' in e.value[0].data)
        ) {
            return
        }

        this.notificationTemplateId = e.value[0].data.textContentNotificationTemplateId
    }
}