import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { Column, Properties } from 'devextreme/ui/data_grid';
import { columns } from '../../config/notification-schedule-plan-details/columns';
import { dataFieldLookupSourceMapping } from '../../config/notification-schedule-plan-details/datafield-lookup-source-mapping';
import { relatedDataValueAndQueryStringKeyMapping } from '../../config/notification-schedule-plan-details/datafield-value-query-string-key-mapping';
import { DataFieldLookUpSourceMapping, RelatedDataValueAndQueryStringKeyMapping } from 'src/app/shared/typing';
import { NotificationSchedulePlan } from '../../config/notification-schedule-plan-details/typing';
import { formatTime } from 'src/app/shared/utils/time-format/utils';

@Component({
  selector: 'app-notification-schedule-plan-details',
  templateUrl: './notification-schedule-plan-details.component.html',
})
export class NotificationSchedulePlanDetailsComponent {
  /**
   * 取得datagrid元件，將之存於變數grid。
   */
  @ViewChild('grid', { static: false }) grid: DxDataGridComponent;

  /**
   * 顯示時間計畫細節data grid的資料，需要從主畫面，
   * [notificationSchedulePlans]{@link RecurringNotificationInfosComponent#notificationSchedulePlans}引入
   */
  @Input() notificationSchedulePlans: NotificationSchedulePlan[]

  /**
   * 雙向綁定是否顯示時間計畫細節的popup，需要從主畫面[isDetailsVisible]{@link RecurringNotificationInfosComponent#isDetailsVisible}引入
   */
  @Input() isDetailsVisible: boolean = false

  /**
   * 雙向綁定，當關閉popup，把[isDetailsVisible]{@link NotificationSchedulePlanDetailsComponent#isDetailsVisible}設為false時，
   * 也需要將主畫面[isDetailsVisible]{@link RecurringNotificationInfosComponent#isDetailsVisible}也設為false
   */
  @Output() isDetailsVisibleChange = new EventEmitter()

  /**
   * devextreme data grid columns
   * 
   * 用來設定grid具有那些欄位。
   * 
   * DevExtreme官方文件: [Column]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Columns/Overview/}
   */
  columns: Column[] = columns;

  /**
   * 選單與資料路徑對應設定檔案
   * 
   * {@link GridFeatureDirective}接收後用來設定[DataGrid]{@link https://js.devexpress.com/Angular/Documentation/23_2/Guide/UI_Components/DataGrid/Getting_Started_with_DataGrid/}的新增、編輯表單裡的選單。
   */
  dataFieldLookupSourceMapping: DataFieldLookUpSourceMapping = dataFieldLookupSourceMapping;

  /**
   * 欄位、值、與回應資料結構的對應設定檔案
   * 
   * 此頁面不會用到，但需要給予空陣列，避免報錯
   */
  relatedDataValueAndQueryStringKeyMapping: RelatedDataValueAndQueryStringKeyMapping = relatedDataValueAndQueryStringKeyMapping;

  /**
   * [grid]{@link NotificationSchedulePlanDetailsComponent#grid}的設定
   * 
   * 關閉刪除、更新功能，引王filter row、toolbar與頁碼部分
   */
  gridProperties: Properties = {
    editing: {
      allowDeleting: false,
      allowUpdating: false
    },
    filterRow: {
      visible: false
    },
    toolbar: {
      visible: false
    },
    pager: {
      visible: false
    },
    height: '85%',
    elementAttr: { class: 'schedule-plan-data-grid' }
  }

  /**
   * devextreme grid datasource
   * 
   * 此頁只用來顯示data grid資料
   * 
   * DevExtreme官方文件: [DataSource]{@link https://js.devexpress.com/Angular/Documentation/23_2/ApiReference/Data_Layer/DataSource/}
   */
  gridDataSource = []

  /**
   * 當[isDetailsVisible]{@link NotificationSchedulePlanDetailsComponent#isDetailsVisible}改變時，
   * 呼叫[getDataSource]{@link NotificationSchedulePlanDetailsComponent#getDataSource}
   */
  ngOnChanges() {
    this.getDataSource()
  }

  /**
   * 將[notificationSchedulePlans]{@link NotificationSchedulePlanDetailsComponent#notificationSchedulePlans}的資料，
   * 整理需要的欄位後給予[gridDataSource]{@link NotificationSchedulePlanDetailsComponent#gridDataSource}
   * 
   * 如果[notificationSchedulePlans]{@link NotificationSchedulePlanDetailsComponent#notificationSchedulePlans}為空，
   * 則顯示無資料
   */
  getDataSource() {
    if (!this.notificationSchedulePlans?.length) {
      return
    }

    this.gridDataSource = this.notificationSchedulePlans.map(plan => ({
      cronDescription: plan.cronDescription,
      name: plan.name,
      pushCount: plan.pushCount,
      nextExpectedParseTime: formatTime(plan.nextExpectedParseTime),
      nextExpectedPushTime: formatTime(plan.nextExpectedPushTime),
      isActive: plan.isActive,
      isFullyFinished: plan.isFullyFinished
    }))
  }

  /**
   * 由於[isDetailsVisible]{@link NotificationSchedulePlanDetailsComponent#isDetailsVisible}使用雙向綁定，
   * 所以在關閉popup時，呼叫[isDetailsVisibleChange]{@link NotificationSchedulePlanDetailsComponent#isDetailsVisibleChange}，
   * 將[RecurringNotificationInfosComponent#isDetailsVisible]{@link RecurringNotificationInfosComponent#isDetailsVisible}也關閉
   */
  closePopup() {
    this.isDetailsVisibleChange.emit(false)
  }
}